'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { motion } from 'framer-motion';
import {
    LayoutDashboard,
    Map,
    Calendar,
    Settings,
    Github,
    Plus,
    ChevronRight,
    Zap
} from 'lucide-react';

const navItems = [
    { href: '/', icon: LayoutDashboard, label: 'Dashboard' },
    { href: '/scans', icon: Map, label: 'Scans & Reports' },
    { href: '/schedules', icon: Calendar, label: 'Schedules' },
    { href: '/settings', icon: Settings, label: 'Settings' },
];

export function Sidebar() {
    const pathname = usePathname();

    return (
        <aside className="w-72 bg-black/40 glass fixed top-0 left-0 h-screen flex flex-col z-50">
            {/* Logo */}
            <div className="p-6 border-b border-white/5">
                <Link href="/" className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center shadow-lg glow-blue">
                        <Zap className="w-5 h-5 text-white" />
                    </div>
                    <div>
                        <h1 className="text-lg font-bold text-white">GridSerp</h1>
                        <p className="text-[10px] text-zinc-500 uppercase tracking-[0.2em] font-medium">Enterprise</p>
                    </div>
                </Link>
            </div>

            {/* Quick Action */}
            <div className="p-4">
                <Link href="/scans/new">
                    <motion.button
                        whileHover={{ scale: 1.02 }}
                        whileTap={{ scale: 0.98 }}
                        className="w-full py-3 px-4 rounded-xl bg-gradient-to-r from-blue-600 to-purple-600 text-white font-semibold flex items-center justify-center gap-2 shadow-lg hover:shadow-blue-500/25 transition-shadow"
                    >
                        <Plus className="w-4 h-4" />
                        New Tracker
                    </motion.button>
                </Link>
            </div>

            {/* Navigation */}
            <nav className="flex-1 px-3 py-4 space-y-1 overflow-y-auto">
                {navItems.map((item) => {
                    const isActive = pathname === item.href ||
                        (item.href !== '/' && pathname.startsWith(item.href));

                    return (
                        <Link key={item.href} href={item.href}>
                            <motion.div
                                whileHover={{ x: 4 }}
                                className={`
                  flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 group relative
                  ${isActive
                                        ? 'bg-white/10 text-white'
                                        : 'text-zinc-400 hover:text-white hover:bg-white/5'
                                    }
                `}
                            >
                                {isActive && (
                                    <motion.div
                                        layoutId="activeNav"
                                        className="absolute left-0 top-1/2 -translate-y-1/2 w-1 h-8 bg-gradient-to-b from-blue-500 to-purple-500 rounded-full"
                                        transition={{ type: "spring", stiffness: 300, damping: 30 }}
                                    />
                                )}
                                <item.icon className="w-5 h-5" />
                                <span className="font-medium">{item.label}</span>
                                <ChevronRight className={`w-4 h-4 ml-auto opacity-0 group-hover:opacity-100 transition-opacity ${isActive ? 'opacity-100' : ''}`} />
                            </motion.div>
                        </Link>
                    );
                })}
            </nav>

            {/* Footer */}
            <div className="p-4 border-t border-white/5">
                <a
                    href="https://github.com/danishfareed/Google-Maps-SERP"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="flex items-center gap-3 px-4 py-3 text-zinc-500 hover:text-zinc-300 transition-colors rounded-xl hover:bg-white/5"
                >
                    <Github className="w-5 h-5" />
                    <span className="text-sm">View on GitHub</span>
                </a>
                <div className="px-4 mt-3">
                    <p className="text-[10px] text-zinc-600">v2.0.0 • Local Hosted</p>
                    <p className="text-[10px] text-zinc-700 mt-1">Built with ❤️ by Danish Fareed</p>
                </div>
            </div>
        </aside>
    );
}
